/**
 * Y,N
 * @desc - _ cell-input
 * @props {iconType: String} - N icon3 arrow,tips,camera
 * @props {label: String} - N 
 * @props {focus: boolean} - N ,  readonly = true 
 * @props {readonly: boolean} - N false
 * @props {align: String} - N left center right
 * @props {placeholder: String} - N 
 * @event {on-focus} -N 
 * @event {on-blur} -N 
 * @event {clickHandler} input,readonly = true 
 * @event {clickIconHandler} iocn
 * @example <cell-input></cell-input>
 * ps import
 */


<template>
	<cell :is-link="iconType === 'arrow'" class="v-cell-input" :class="iconType">
		<div class="v-cell-input-bd" slot="cell-bd">
			<div class="label"><label>{{label}}</label></div>
			<div class="inputs" :class="inputFocuscClass">
				<span v-if="focus && readonly"><i>{{value}}</i></span>

				<input
					:readonly="readonly"
					:style="styles"
					:value="value"
					:placeholder="placeholder"
					@click.stop="cellInputClick"
					@input="oninput"
					@focus="$emit('on-focus')"
					@blur="$emit('on-blur')"
					:maxlength="maxlength"
				>
			</div>
		</div>
		<div v-if="showIcon" slot="cell-icon" class="custom-icon" @click="$emit('clickIconHandler')">
			<i :class="icon" :style="iconStyle"></i>
		</div>
	</cell>
</template>
<script>
	import props from './utils/props'
	import cell from '../cell'
	export default {
		name: 'cell-input',
		props,
		methods: {
			clickHandler(e){
				this.$emit('clickHandler')
			},
			cellInputClick(){
				if(this.readonly){
					this.$emit('clickHandler')
				}
			},
			oninput({target}){
				this.$emit('input', target.value)
			}
		},
		computed: {
			styles(){
				return {
					'text-align': this.align
				}
			},
			icon(){
				return {
					'iconfont': true,
					'svg-explain_linear': this.iconType === 'tips',
					'svg-camera': this.iconType === 'camera'
				}
			},
			iconStyle(){
			  let style = {}
			  if(this.iconType === 'tips'){
			    let tips = require('../../common/svg/explain_linear.svg')
			    style={
            background: `url(${tips}) no-repeat center`,
            backgroundSize: '100%'
          }
        }else if(this.iconType === 'camera'){
          let tips = require('../../common/svg/camera.svg')
          style={
            background: `url(${tips}) no-repeat center`,
            backgroundSize: '100%'
        }
        }
				return style
			},
			showIcon(){
					if(this.iconType === 'tips' || this.iconType === 'camera') return true
			},
			inputFocuscClass(){
				if(this.align === 'left'){
					return {
						'focus-left': true
					}
				}

				if(this.align === 'right'){
					return {
						'focus-right': true
					}
				}
				return Object.create(null)
			}
		},
		components: {
			cell
		}
	}
</script>
<style lang="scss">
	@import './scss/index.scss';
</style>



