/**
 * Y,N
 * @desc - ,
 * @props {disabled: Boolean} N- 
 * @props {type: String} N- cm,cg cm
 * @event {clickHandler: Function} N- 
 * @example <global-big-button @clickHandler="">
 *				<slot></slot>
 *			</global-big-button>
 * ps 
 */

<template>
	<div class="globalBigButton-warp">
		<v-button @clickHandler="$emit('clickHandler')" :type="type" :disabled="disabled">
			<slot></slot>
		</v-button>
	</div>
</template>
<script>
	import props from './utils/props'
	export default {
		name: 'globalBigButton',
		props,
    components:{
    }
	}
</script>
<style lang="scss">
	@import './scss/index.scss';
</style>



