<template>
  <div>
    <group title="toast/">
      <v-button @clickHandler="showToast1"></v-button>
    </group>
    <group title="toast/">
      <v-button @clickHandler="showToast2"></v-button>
    </group>
    <group title="toast/">
      <v-button @clickHandler="showToast3"></v-button>
    </group>
  </div>
</template>
<script>
import Group from '../../fxjcui/extend/group/index'
import ToastPlugin from '../../fxjcui/extend/toast/plugin'
import Vue from 'vue'
Vue.use(ToastPlugin,{
  position:'middle'
})
export default {
  components: {
    Group
  },
  data() {
    return {}
  },
  mounted(){

  },
  methods: {
    showToast1(){
      this.$toast.show({
        type:'text',
        text:'asdfasdfasdf'
      })
    },
    showToast2(){
      this.$toast.show({
        text:'toastsdfgsfgsdfgsdfgsdfgsdfgsdfgsdfgdfsgdsfgfsdgsfdg',
      })
    },
    showToast3(){
      this.$toast.show({
        type: 'cancel',
        text:'toastsdfgsfgsdfgsdfgsdfgsdfgsdfgsdfgdfsgdsfgfsdgsfdg',
        time:20000000
      })
    },
  }
}
</script>



