<template>
  <div>
    <group title="cell">
      <cell :title="{desc:''}" :isLink="true" :value="{desc:'',isBreak: false, align:'left'}"></cell>
      <cell :title="{desc:''}" :value="{desc:''}"></cell>
      <cell :title="{desc:''}" :isLink="true" :value="{desc:''}"></cell>
      <cell :title="{desc:''}" :isLink="true"></cell>
    </group>
    <group :top="true">
      <cell :title="{desc:''}" :value="{desc:'',subDesc:''}" :isLink="true"></cell>
      <cell :title="{desc:'',subDesc:''}" :isLink="true"></cell>
      <cell :title="{desc:'',subDesc:''}" :value="{desc:''}" :isLink="true"></cell>
    </group>
    <group :top="true">
      <cell :text="{desc:'6'}" :isLink="true"></cell>
      <cell :text="{desc:'6', isBreak:true}" :isLink="true"></cell>
    </group>
    <group title="">
      <cell :title="{desc:'',subDesc:''}"
            :value="{desc:addressInfo.names.join(' ')}"
            :isLink="true"
            @clickHandler="clickHandler"
      ></cell>
    </group>
    <group title="">
      <cell :title="{desc:''}">
        <inline-switch v-model="sswitch" slot="cell-ft" @clickHandler="oc"></inline-switch>
      </cell>
    </group>

    <group title="/">
      <cell-input type="password" align="left" v-model="word" :readonly="true" :focus="setF" @clickHandler="setFoucs"></cell-input>
    </group>


    <group title="//">
      <cell-input iconType="tips" align="left" @on-focus="onFoucs" @on-blur="onBlue" placeholder=""></cell-input>
      <cell-input iconType="camera" align="left" placeholder="" @clickIconHandler="clickIconHandler"></cell-input>
    </group>

    <group :top="true">
      <cell-input label="" v-model="word"></cell-input>
      <cell-input label="" v-model="word"></cell-input>
      <cell-input label="" v-model="word"></cell-input>
    </group>
    <group :top="true" title="">
      <cell-input label="" v-model="word" iconType="arrow" align="right"></cell-input>
      <cell-input label="" v-model="word" align="right"></cell-input>
    </group>
    <group title="">
      <msg-code v-model="msgVC" startNum="10" keyboardType="nat" :hasRpc="false" @focusHandler="focusInput" @clickFetch="clickFetch" @blurHandler="blurHandler"></msg-code>
    </group>
    <number-keyboard v-model="keyboard" :word.sync="word"></number-keyboard>
    <number-keyboard v-model="msgkeyboard" :word.sync="msgVC"></number-keyboard>
  </div>
</template>
<script>
import Group from '../../fxjcui/extend/group/index'
import Cell from '../../fxjcui/extend/cell/index'
import CellInput from '../../fxjcui/extend/cellInput/index'
import InlineSwitch from '../../fxjcui/extend/inlineSwitch/index'
import NumberKeyboard from '../../fxjcui/extend/numberKeyboard/index'
import MsgCode from '../../fxjcui/extend/msgCode/index'

export default {
  components: {
    Group,
    Cell,
    InlineSwitch,
    CellInput,
    NumberKeyboard,
    MsgCode
  },
  data() {
    return {
      msgVC:'',
      word: '',
      setF:false,
      sswitch: false,
      keyboard:false,
      msgkeyboard:false,
      addressInfo: {
        names: [''],
        ids: []
      },
      select: ["440000", "440100", "440111"],
    }
  },
  methods: {
    blurHandler () {
    },
    focusInput(e, cb) {
      this.msgkeyboard =true
    },
    clickFetch (startCount, stopCount) {
      console.log('""');
      startCount(); // 
      setTimeout(() => {
        stopCount() // 
      }, 10000);
    },
    onFoucs(){
      console.log(`onFoucs`)
    },
    onBlue(){
      console.log('onBlue')
    },
    clickIconHandler(){
      console.log('clickIconHandler')
    },
    setFoucs(){
      this.setF = true
      // App.Native.showKeyboard()
      this.keyboard = true
    },
    oc(){
      this.sswitch = !this.sswitch
    },
    clickHandler(){
      console.log('')
    },
  }
}
</script>



