/**
 * Y,N
 * @desc - cell,cell-input
 * @props {title: String} - N 
 * @props {top: Boolean} - N 
 * @slot {name:title} - slot
 * @example <v-group title="" :top="true">
 *             <cell></cell>
 *             <cell-input></cell-input>
 *          </v-group>
 * ps import
 */

<template>
	<div class="v-group" :style="top && 'margin-top:.24rem'">
		<div class="v-group-title">
			<span v-if="title" class="default-title">{{title}}</span>
			<slot name="title"></slot>
		</div>
		<div class="v-group-content">
			<slot></slot>
		</div>
	</div>
</template>
<script>
	import props from './utils/props'
	export default {
		name: 'v-group',
		props
	}
</script>
<style lang="scss">
	@import './scss/index.scss';
</style>



