export default {
	item: {
		type: Array,
		default(){
			return Object.freeze([
				{title:'',message:true},
				{title:'',message:false},
				{title:'',message:false},
				{title:'',message:false},
				{title:'',message:false},
			])
		}
	},
	selected: {
		type: [Number,String],
		default: 0
	},
	shadow: {
		type: Boolean,
		default: true
	}
}


