/**
 * Y,N
 * @desc - ,head,main,footer, headfooter,mainheadfooter
 * @props { namespace: String } N , url
 * @example <v-page namespace="xxx">
 *             <v-head></v-head>
 *             <div class="main"></div>
 *             <div></div>
 *          </v-page>
 *
 * ps  main class
 */



<template>
	<div class="router-view" :class="namespaceClass">
		<div :class="`drawer-view`">
			<slot></slot>
		</div>
	</div>
</template>
<script>
	import props from './utils/props'
	export default {
		name: 'vPage',
		data(){
			return {}
		},
		props,
		created(){
			if(this.namespace){
				this.namespaceClass = this.namespace
			}else{
				let [a, b, moduleName, pageName] = window.location.pathname.split('/')
				this.namespaceClass = moduleName.replace(/cdbank_/, '') + '_' + pageName.replace(/.html/, '')
			}
		}
	}
</script>



