<template>
  <div>
    <group title="/cm">
      <div class="flexx">
        <v-button size="m"></v-button>
        <v-button size="s"></v-button>
      </div>
    </group>
    <group title="/cm/plain">
      <div class="flexx">
        <v-button size="m" :plain="true"></v-button>
        <v-button size="s" :plain="true"></v-button>
      </div>
    </group>
    <group title="/cm/">
      <div class="flexx">
        <v-button size="m" :disabled="true" @clickHandler="click"></v-button>
        <v-button size="s" :disabled="true"></v-button>
      </div>
    </group>
    <group title="/cm/plain/">
      <div class="flexx">
        <v-button size="m" :disabled="true"></v-button>
        <v-button size="s" :disabled="true"></v-button>
      </div>
    </group>
    <group title="/cg">
      <div class="flexx">
        <v-button size="m" type="cg"></v-button>
        <v-button size="s" type="cg"></v-button>
      </div>
    </group>

    <group title="/cg/plain">
      <div class="flexx">
        <v-button size="m" type="cg" :plain="true"></v-button>
        <v-button size="s" type="cg" :plain="true"></v-button>
      </div>
    </group>

    <group title="/cg/">
      <div class="flexx">
        <v-button size="m" type="cg" :disabled="true"></v-button>
        <v-button size="s" type="cg" :disabled="true"></v-button>
      </div>
    </group>

    <group title="/cg/plain/">
      <div class="flexx">
        <v-button size="m" type="cg" :plain="true" :disabled="true"></v-button>
        <v-button size="s" type="cg" :plain="true" :disabled="true"></v-button>
      </div>
    </group>

    <group title="/co">
      <div class="flexx">
        <v-button size="m" type="co"></v-button>
        <v-button size="s" type="co"></v-button>
      </div>
    </group>

    <group title="/co/">
      <div class="flexx">
        <v-button size="m" type="co" :disabled="true"></v-button>
        <v-button size="s" type="co" :disabled="true"></v-button>
      </div>
    </group>


    <group title="/cm/cg">
      <p style="padding:0.1rem"></p>
      <v-button></v-button>
      <p style="padding:0.1rem"></p>
      <v-button :plain="true"></v-button>
      <p style="padding:0.1rem"></p>
      <v-button type="cg"></v-button>
      <p style="padding:0.1rem"></p>
      <v-button type="cg" :plain="true"></v-button>
      <p style="padding:0.1rem"></p>
      <v-button type="co" :plain="true"></v-button>
      <p style="padding:0.1rem"></p>
    </group>

    <group title="">
      <global-button-multiple></global-button-multiple>
    </group>
    <group title="/">
      <global-button-multiple :disabled="true"></global-button-multiple>
    </group>

    <group title="">
      <global-big-button>
        <b style="padding:0 0.1rem">199999.00</b>
      </global-big-button>
    </group>

    <group title="/type1">
      <trade-button type="cg" slot-type="type1" @clickHandler="click" button-text="">
        <template slot="type1">
          <span class="tip">:</span>
          <span class="number">144</span>
          <span class="currency"></span>
        </template>
      </trade-button>
    </group>
    <group title="/type2">
      <trade-button type="cm" slot-type="type2" @clickHandler="click" button-text="">
        <template slot="type2">
          <span class="number">144</span>
          <span class="currency"></span>
        </template>
      </trade-button>
    </group>
    <group title="/type3">
      <trade-button type="cg" slot-type="type3" @clickHandler="click" button-text="">
        <template slot="type3">
          <span class="number">144<span class="currency"></span>+<span>2323424</span><span class="currency"></span></span>
        </template>
      </trade-button>
    </group>
    <group title="/type4">
      <trade-button slot-type="type4" @clickHandler="click" button-text="">
        <template slot="type4">
          <span class="number">144<span class="currency"></span></span>
          <span class="desc">10</span>
        </template>
      </trade-button>
    </group>
  </div>
</template>
<script>
import Group from '../../fxjcui/extend/group/index'
import GlobalButtonMultiple from '../../fxjcui/extend/globalButtonMultiple/index'
import GlobalBigButton from '../../fxjcui/extend/globalBigButton/index'
import TradeButton from '../../fxjcui/extend/tradeButton/index'

export default {
  components: {
    Group, GlobalButtonMultiple, GlobalBigButton, TradeButton
  },
  data() {
    return {}
  },
  methods: {
    click() {
      console.log('')
    }
  }
}
</script>



