/**
 * Y,N
 * @desc -  v-inline-switch
 * @props {value: Boolean} - N 
 * @props {disabled: Boolean} - N 
 * @event {clickHandler: function} -N 
 * ps import
 */

<template>
	<div class="v-inline-witch" :class="switchClass" @click="$emit('clickHandler')"></div>
</template>
<script>

	import props from './utils/props'
	export default {
		name: 'v-inline-switch',
		props,
		computed: {
			switchClass(){
				return {
					'flag-true': this.value,
					'flag-false': !this.value
				}
			}
		}
	}
</script>
<style lang="scss">
	@import './scss/index.scss';
</style>



